import React from 'react';
import './Profile.css'
import { useSelector } from 'react-redux';

export const Profile = () => {

  const {fname,lname,username} = useSelector(state => state.Auth.user);

  return (
    <div className='container'>
      <div className="mainSetting">
        <div>
          <img className="profileImg" src="/images/user.jpg" alt="Profile pic"  />
        </div>
        <div className="name">
          <h2>{fname} {lname}</h2>
          <span class="badge bg-primary">@{username}</span>
          <div className='mt-2 d-flex '>
            <p>32 Followers</p>
            <p className='ms-3'>3 Following</p>
          </div>
        </div>
      </div>

      <div className="clientSetting">

        <div className="tab">
        <h5 className='me-5'>General Settings</h5>
        <h5 className='me-5'>Privacy and Passcode</h5>
        <h5 className='me-5'>Policy Agreement</h5>
        <h5>Followers / Following</h5>
        </div>
      </div>

    </div>
  )
}

export default Profile;
