import {React, useState} from 'react'
import Avatar from '../../common/avatar/Avatar'
import QsnBox from '../qsnBox/QsnBox'
import "./Article.css";
import Modal from 'react-responsive-modal';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

const Article = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    return (
        <>
            <QsnBox />
            <h5 className='mt-3 mb-3 fw-bold'><u>Recent Questions</u></h5>

            <div class="card ArticleCard mb-3">
                <div className="post__info">
                    <Avatar width={"30px"} />
                    <h5 className='mt-1'>@bimalstha <i class="fa-solid fa-user-plus fs-6"></i></h5>
                    <small>posted on 24 jan 2022</small>
                </div>
                <div className="post__body justify-content-between">
                    <h5>Lorem ipsum dolor sit amet consectetur adipisicing elit. Reiciendis officiis quam possimus, magni corporis nulla accusamus autem ratione debitis in earum eaque! Perferendis tempore, facere veritatis nulla ab dicta saepe!</h5>
                    
                    <button onClick={() => setIsModalOpen(true)} className='btn shadow-0 btn-sm btn-danger'>
                        Answer
                    </button>
                </div>
                <hr />
                <div className="post__answer">
                    <h6 className='fw-bold'>Answered by <u><a href="#"> Roshan Lama </a></u>on 7th July 1998 </h6>
                    <p className='mt-3 mb-3'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Beatae, unde. Beatae porro debitis non facilis officiis quis odit, doloremque ipsum soluta quibusdam recusandae enim sunt architecto minus molestias illo nostrum!</p>
                    <img src="/images/answer.jpg" alt="" width={"100%"} />
                </div>
                <div className="post__footer d-flex justify-content-between">
                    <div className="post_footerAction">
                        <button type="button" class="btn btn-outline-light" data-mdb-ripple-color="dark">1k+ <i class="fa-solid fa-circle-arrow-up"></i></button>
                        <button type="button" class="btn ms-2 btn-outline-light" data-mdb-ripple-color="dark">53 <i class="fa-solid fa-circle-arrow-down"></i></button>
                        <button type="button" class="btn ms-2 btn-outline-light" data-mdb-ripple-color="dark"><i class="fa-solid fa-comments"></i></button>
                        <button type="button" class="btn ms-2" data-mdb-ripple-color="dark"><i class="fa-solid fa-share"></i></button>
                    </div>
                    <div className="rightSide">
                        <button type="button" class="btn ms-2 fs-6 shadow-0" data-mdb-ripple-color="dark"><i class="fa-solid fa-bookmark"></i></button>

                    </div>
                </div>
            </div>

            <Modal classNames={"answerModel"}
            center closeOnEsc open={isModalOpen}
            >
                <div className="modal__question">
                    <h4><b>This is test questions</b></h4>
                    <p>Asked by <u>Ashok Dorje</u> on <u>9th March 2022</u></p>
                </div>
                <hr />
                <div className="modal__answer">
                    <ReactQuill placeholder='Enter your thoughts' />
                </div>
                <div className="modal__buttons d-flex mt-3 justify-content-between">
                    <button onClick={() => setIsModalOpen(false)} className='btn btn-danger w-100'>Close</button>
                    <button className='btn btn-primary ms-1 w-100'>Add Answer</button>
                </div>
                
                
            </Modal>

        </>
    )
}

export default Article